.App {
    color: #333;
    margin: 0 auto;
    padding: 40px;
    background-color: rgba(255,255,255,0.5);
    min-height: calc(100vh - 60px);
}

.App-Header {
    display: flex;
    align-items: flex-end;
    color: #333;
}

.App-Title {
    font-size: 2em; 
}


.App a:link,
.App a:visited,
.App a:active,
.App a:hover {
    color: #e97200;
    text-decoration: underline;
}

.App .video-box {
    position: fixed;
    display: flex;
    overflow: hidden;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    justify-content: center;
    align-items: center;
}