.Disclaimer {
    display: flex;
    flex-direction: column;
    height: calc(100% - 40px);
}

.Disclaimer * {
    flex: 0 0 auto;
}


.Disclaimer h1 {
    color: #ff9000;
    font-size: min(8vw, 2.5em);
    text-align: center;
}

.Disclaimer .legal {
    max-width: 400px; 
    margin: 0 auto 20px; 
    overflow: auto;
    flex: 1 1 auto;
}

.Disclaimer .btn.btn-primary {
    background-color: #ff9000 !important;
    border-color: #ff9000 !important;
}